:root {
  --orange: #f87719;
  --total-black: #111;
  --ligth-black: #282828;
  --grey: #595959;
  --ligth-grey: #b7b7b7;
  --ligth-white: #f8f8f8;
  --total-white: #fff;
  --red: #f84119;
  --yellow: #ffc226;
  --shadow: #01010140;
  --linear-btn: linear-gradient(145deg, var(--yellow) 0%, var(--red) 100%);
  --linear-star: linear-gradient(145deg, var(--red) 0%, #f89f19ad 100%);
}

.container {
  max-width: 320px;
  margin: 0 auto;
  padding: 0 20px;
}

@media screen and (width >= 768px) {
  .container {
    max-width: 768px;
    padding: 0 32px;
  }
}

@media screen and (width >= 1280px) {
  .container {
    max-width: 1280px;
    padding: 0 32px;
  }
}

*, :before, :after {
  box-sizing: border-box;
}

html {
  scroll-behavior: auto;
}

@media screen and (prefers-reduced-motion: reduce) {
  html {
    scroll-behavior: auto;
  }
}

h1, h2, h3, h4, h5, h6, p {
  margin: 0;
}

p:last-child {
  margin-bottom: 0;
}

ul {
  margin: 0;
  padding: 0;
  list-style: none;
}

a {
  color: currentColor;
  text-decoration: none;
}

button {
  cursor: pointer;
}

img {
  max-width: 100%;
  height: auto;
  display: block;
}

picture {
  display: block;
}

.list {
  list-style: none;
}

.link {
  text-decoration: none;
}

body {
  color: var(--total-white);
  font-family: Roboto, sans-serif;
}

body {
  color: var(--total-white);
  background-color: var(--total-black);
  scrollbar-width: none;
